import { IntroPage } from './../intro/intro';
import { Answer } from './../../providers/questionary/questionary';
import { Component, ViewChildren, QueryList, ElementRef } from '@angular/core';
import { AlertController, IonicPage, LoadingController, NavController, MenuController, ToastController, ActionSheetController } from 'ionic-angular';
import { Questionary, QuestionaryProvider } from "../../providers/questionary/questionary";
import { DatabaseProvider } from "../../providers/database/database";
import { Storage } from "@ionic/storage";
import { City } from "../../providers/city/city";
import { Plan } from "../../providers/plan/plan";
import { Respondent, Discipline } from "../respondent-profile/respondent-profile";
import { Question, QuestionProvider } from "../../providers/question/question";
import { PrioritizationProvider } from "../../providers/prioritization/prioritization";
import { MetricItem } from '../prioritization/prioritization';
import { RestProvider } from '../../providers/rest/rest';

@IonicPage()
@Component({
  selector: 'page-questionaries-list',
  templateUrl: 'questionaries-list.html',
})

export class QuestionariesListPage {
  @ViewChildren('achievementIcons') achievementIcons: QueryList<ElementRef>;
  achievementList: Achievement[] = [];
  disciplineList: Discipline[] = [];
  questionaries: Questionary[] = [];
  respondent: Respondent;
  plan: any;
  questionary: Questionary = new Questionary();
  questions: any;
  answers: any[] = [];
  metricItems: any;
  jobCity: City;
  btnContinueDisabled: boolean = true;
  loader: any;
  progress: number;
  points: number;
  level: string = "Nível 0";
  levelImg: string = "level0-min";
  totalQuestionaries: number;
  answeredQuestionaries: number;
  useGame: boolean = false;
  personAnimation: string;
  personQuote: any;

  constructor(public toastCtrl: ToastController, public menuCtrl: MenuController, public navCtrl: NavController, private databaseProvider: DatabaseProvider, private questionProvider: QuestionProvider, private storage: Storage, private questionaryProvider: QuestionaryProvider, public loadingCtrl: LoadingController, private alertCtrl: AlertController, private priorizationProvider: PrioritizationProvider, private actionSheetCtrl: ActionSheetController, public restProvider: RestProvider) {
    this.answeredQuestionaries = 0;
    this.totalQuestionaries = 0;
    this.progress = 0;
    this.plan = new Plan();
    this.resolvePerson();
    let entities = ['plan', 'questionaries', 'respondent'];
    this.databaseProvider.verifyEntities(entities)
      .then((data) => {
        if (data.length > 0 && data.filter((data) => data == false).length == 0) {
          this.setEntities();
        } else {
          this.navigateBack();
        }
      }).catch(() => this.navigateBack());
    this.storage.get('useGame').then(data => {
      this.useGame = data;
      if (this.useGame) {
        this.storage.get('achievementList').then(data => {
          if (!data)
            this.loadAchievement();
          else
            this.achievementList = data;
          this.storage.get('helpHome')
            .then((data) => {
              if (data == null)
                this.help();
            });
        });
      }
    });
    this.storage.get('disciplineSelected').then(data => this.disciplineList = data);
  }

  //Verifica se as entidades estão armazenadas no IONIC Storage
  setEntities() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.storage.get('plan')
      .then((data) => {
        this.plan = data;
        this.questionaryProvider.getAllQuestionariesByPlan(this.plan)
          .then((data: Questionary[]) => {
            this.storage.set('questionaries', data);
            this.questionaries = data;
            this.storage.get('respondent')
              .then((data) => {
                this.respondent = data;
                this.questionaryProvider.getAllPlanQuestionariesAnsweredByRespondent(this.plan, this.questionaries, this.respondent)
                  .then((data: Questionary[]) => {
                    if (data != null) {
                      //--------------IMPORTANTE---------------
                      //-------SELEÇÃO DOS QUESTIONÁRIOS PELO TIPO DO RESPONDENTE-------
                      //------------------------- CASE 1 POSCOMP -------------------------
                      if (this.respondent.type == "Discente evadido") {
                        data = data.filter(item =>
                          item.name === "POSCOMP - Evasão Discente"
                        );
                      } else if (this.respondent.type == "Discente formado") {
                        data = data.filter(item =>
                          item.name === "POSCOMP - Discente Egresso"
                        );
                      } else if (this.respondent.type == "Discente") {
                        let dataTemp: Questionary[] = [];
                        this.disciplineList.forEach(discipline => {
                          let questTemp = data.filter(quest =>
                            quest.name.endsWith(discipline.name)
                          );
                          questTemp.forEach(q => {
                            dataTemp.push(q);
                          })
                        });
                        data = data.filter(item => (
                          item.name === "POSCOMP - Avaliação da Secretaria" ||
                          item.name === "POSCOMP - Avaliação da Coordenação"
                        ));
                        dataTemp.forEach(dat =>
                          data.push(dat)
                        );
                      } else if (this.respondent.type == "Docente") {
                        data = data.filter(item => (
                          item.name === "POSCOMP - Avaliação da Secretaria" ||
                          item.name === "POSCOMP - Avaliação da Coordenação"
                        ));
                      }
                      //------------------------- CASE 1 POSCOMP -------------------------
                      for (let i = 0; i < data.length; i++) {
                        if (data[i].answered) {
                          this.progress = this.progress + 1;
                          if (this.achievementList.length > 0) {
                            if (!this.achievementList[i].unlocked) {
                              this.achievementList[i].unlocked = true;
                              this.showAchievementUnlock(this.achievementList[i]);
                            }
                          }
                        }
                      }
                      this.totalQuestionaries = data.length;
                      this.answeredQuestionaries = this.progress;
                      this.progress = this.progress / data.length;
                      this.progress = this.progress * 100;
                      this.questionaries = data;
                      this.storage.set('achievementList', this.achievementList);
                      this.loader.dismiss();
                    } else {
                      this.questionaries = [];
                      this.showAlertGetAllPlanQuestionariesAnsweredByRespondent();
                    }
                  }).catch((error) => {
                    console.error(error);
                    this.restProvider.sendGoogleAnalyticsErrorData(
                      'QuestionariesListPage',
                      'setEntities',
                      error);
                    this.showAlertGetAllPlanQuestionariesAnsweredByRespondent();
                  });
              }).catch((error) => {
                console.error(error);
                this.restProvider.sendGoogleAnalyticsErrorData(
                  'QuestionariesListPage',
                  'setEntities',
                  error);
                this.showAlertGetRespondent();
              });
          }).catch((error) => {
            console.error(error);
            this.restProvider.sendGoogleAnalyticsErrorData(
              'QuestionariesListPage',
              'setEntities',
              error);
            this.showAlertGetAllQuestionariesByPlan();
          });
      }).catch((error) => {
        console.error(error);
        this.restProvider.sendGoogleAnalyticsErrorData(
          'QuestionariesListPage',
          'setEntities',
          error);
        this.showAlertGetAllPlan();
      });
    this.storage.get('points').then((data) => {
      this.points = data;
      this.resolveLevel();
    });
  }

  loadAchievement() {
    this.achievementList = [];
    this.achievementList = [
      { id: 1, icon: "0", unlocked: false, name: "Dicas para estimular sua inteligência emocional" },
      { id: 2, icon: "1", unlocked: false, name: "Dicas para lidar com o estresse" },
      { id: 3, icon: "2", unlocked: false, name: "Dicas para se proteger do medo" },
      { id: 4, icon: "3", unlocked: false, name: "Dicas para melhorar a qualidade do sono" },
      { id: 5, icon: "4", unlocked: false, name: "Estratégias para facilitar o aprendizado e memorização" }
    ];
    this.storage.set('achievementList', this.achievementList);
  }

  resolveLevel() {
    if (this.points > 0 && this.points <= 12) {
      this.level = "Bronze";
      this.levelImg = "level1-min";
    }
    if (this.points > 12 && this.points <= 21) {
      this.level = "Prata";
      this.levelImg = "level2-min";
    }
    if (this.points > 21) {
      this.level = "Ouro";
      this.levelImg = "level3-min";
    }
  }

  //Carrega as métricas e vai para a página de priorização
  loadMetrics(metricId) {
    //------------------------CARREGA OS ITENS DE MÉTRICAS-----------------------------
    //1 - GUT, 2 - ESCALA QUALITATIVA, (3-17) - Métricas do questionário de teste
    this.priorizationProvider.getMetricValuesMetric(metricId)
      .then((metricItems: MetricItem[]) => {
        if (metricItems != null) {
          this.metricItems = metricItems;
          this.storage.set('metricItems', this.metricItems);
          this.insertAnswerAndNavigateToPrioritization();
        } else {
          this.showAlertLoadMetrics(metricId);
        }
      }).catch((error) => {
        console.error(error);
        this.restProvider.sendGoogleAnalyticsErrorData(
          'QuestionariesListPage',
          'loadMetrics',
          error);
        this.showAlertLoadMetrics(metricId);
      });
  }

  //Seleciona o questionário
  setQuestionary(questionary) {
    this.questionary = questionary;
    this.btnContinueDisabled = false;
  }

  //Consulta todas as questões do questionário
  getAllQuestionsByQuestionary(questionary, isRuralZone) {
    this.questionProvider.getAllQuestionsByQuestionary(questionary, isRuralZone)
      .then((result: Question[]) => {
        if (result != null) {
          this.questions = result;
        } else {
          this.showAlertGetAllQuestionsByQuestionary(questionary, isRuralZone);
        }
      }).catch((error) => {
        console.error(error);
        this.restProvider.sendGoogleAnalyticsErrorData(
          'QuestionariesListPage',
          'getAllQuestionsByQuestionary',
          error);
        this.showAlertGetAllQuestionsByQuestionary(questionary, isRuralZone);
      });
  }

  //Exibe os resultados do questionário
  getResults(quesitonarySelected) {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.navCtrl.push('ResultsPage', {
      questionary: quesitonarySelected,
    }).then(this.loader.dismiss());
  }

  //Navega para a pagina de prioritization
  insertAnswerAndNavigateToPrioritization() {
    this.createAnswer();
    this.navigatePrioritizationPage();
  }

  //Cria a resposta
  createAnswer() {
    let answer = new Answer();
    answer.plan = this.plan;
    answer.questionary = this.questionary;
    answer.question = this.questions[0];
    answer.respondent = this.respondent;
    answer.answer = 1;
    let dateTime = new Date();
    answer.created_at = new Date(dateTime.valueOf() - dateTime.getTimezoneOffset() * 60000).toISOString();
    answer.isCompleted = false;
    this.answers.push(answer);
  }

  //Retorna para a página de perfil
  navigateBack() {
    this.navCtrl.setRoot('RespondentProfilePage', {});
  }

  //Navegação para as perguntas
  navigate() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.storage.set('plan', this.plan).then(() => {
      //Consulta as questões do questionário
      this.questionProvider.getAllQuestionsByQuestionary(this.questionary, 0)
        .then((result: Question[]) => {
          if (result != null) {
            this.questions = result;
            //se metric_id for nulo, navega para página de questionário
            if (this.questions[0].metricId == null) {
              this.navigateQuestionaryPage();
              //Se houver metric_id, carrega as métricas específicas  e navega para página de priorization
            } else {
              this.loadMetrics(this.questions[0].metricId);
            }
          } else {
            this.showAlertGetAllQuestionsByQuestionaryNavigate();
          }
        }).catch((error) => {
          console.error(error);
          this.restProvider.sendGoogleAnalyticsErrorData(
            'QuestionariesListPage',
            'navigate',
            error);
          this.showAlertGetAllQuestionsByQuestionaryNavigate();
        });
    }).catch((error) => {
      console.error(error);
      this.restProvider.sendGoogleAnalyticsErrorData(
        'QuestionariesListPage',
        'navigate',
        error);
      this.showAlertSetPlan();
    });
  }

  //Navegação para página do questionário
  navigateQuestionaryPage() {
    this.navCtrl.push('QuestionaryPage', {
      points: this.points,
      questionary: this.questionary,
      currentQuestionIndex: 0,
      currentQuestion: new Question(),
      answers: [],
      answersNeighborhoods: [],
      prioritizations: []
    }).then(this.loader.dismiss());
  }

  // Navegação para página de priorização
  navigatePrioritizationPage() {
    this.navCtrl.push('PrioritizationPage', {
      points: this.points,
      plan: this.plan,
      respondent: this.respondent,
      questionary: this.questionary,
      questions: this.questions,
      metricItems: this.metricItems,
      currentMetricItem: this.metricItems[0],
      currentMetricItemIndex: 0,
      currentQuestionIndex: 0,
      currentQuestion: this.questions[0],
      answers: this.answers,
      answersNeighborhoods: [],
      prioritizations: []
    }).then(this.loader.dismiss());
  }

  showAlertLoadMetrics(metricId) {
    this.metricItems = [];
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.loadMetrics(metricId);
        }
      }]
    });
    alert.present();
  }

  showAlertGetAllPlanQuestionariesAnsweredByRespondent() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados dos questionários. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.setEntities();
        }
      }]
    });
    alert.present();
  }

  showAlertGetRespondent() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Erro ao acessar os dados do respondente. Por favor, preencha novamente.',
      buttons: [{
        text: "Ok",
        handler: () => {
          this.navigateBack();
        }
      }]
    });
    alert.present();
  }

  showAlertGetAllQuestionariesByPlan() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados de questionários. Por favor, tente novamente.',
      buttons: [{
        text: "Ok",
        handler: () => {
          this.navCtrl.setRoot(IntroPage, {});
        }
      }]
    });
    alert.present();
  }

  showAlertGetAllPlan() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível os dados do plano. Por favor, tente novamente.',
      buttons: [{
        text: "Ok",
        handler: () => {
          this.navCtrl.setRoot(IntroPage, {});
        }
      }]
    });
    alert.present();
  }

  showAlertGetAllQuestionsByQuestionaryNavigate() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os questões do questionário. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.navigate();
        }
      }]
    })
    alert.present();
  }

  showAlertSetPlan() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível armazenar os dados do plano. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.navigate();
        }
      }]
    });
    alert.present();
  }

  showAlertGetAllQuestionsByQuestionary(questionary, isRuralZone) {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados das questões. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.getAllQuestionsByQuestionary(questionary, isRuralZone);
        }
      }]
    });
    alert.present();
  }

  showAchievementAlert() {
    let alert = this.alertCtrl.create({
      title: '<img class="img-alert" src="assets/imgs/game/achievement.png"/>',
      cssClass: 'achievement-alert',
      message:
        '<div class="tdialogue-box-text">'
        + '<div>Responda os questionários para liberar as</div>'
        + '<div text-center><strong>Dicas de saúde mental!</strong></div>'
        + '</div>',
      buttons: [{
        text: 'Ok!',
        cssClass: 'achievement-alert-button-confirm',
        handler: () => {
          let quote = "Você só pode desbloquear as <strong>dicas de saúde mental</strong> após responder os <strong>questionários</strong>!"
          this.talkAnimation(quote);
          this.achievementList.forEach(achievement => {
            if (!achievement.unlocked) {
              this.achievementIcons.toArray()[parseInt(achievement.icon)].nativeElement.className = "sonar-wave";
              setTimeout(() => {
                this.achievementIcons.toArray()[parseInt(achievement.icon)].nativeElement.classList.remove("sonar-wave");
              }, 10000);
            }
          });
        }
      }]
    });
    alert.present();
    this.restProvider.sendGoogleAnalyticsEventTag(
      'Questionary List',
      'Clicou na conquista bloqueada',
      '',
      'Game');
  }

  showAchievementUnlock(achievement: Achievement) {
    let alert = this.alertCtrl.create({
      title: '<img class="img-alert" src="assets/imgs/game/achievement-msg-' + achievement.icon + '.png"/>',
      cssClass: 'achievement-alert',
      message:
        '<div class="tdialogue-box-text">'
        + '<div><strong>Você desbloqueou uma dica:</strong></div>'
        + '<div><strong>' + achievement.name + '</strong></div>'
        + '<div text-center>Confira agora!</div>'
        + '</div>',
      buttons: [{
        text: 'Fechar',
        cssClass: 'achievement-alert-button-close',
        handler: () => {
          let quote = "Você desbloqueou uma dica de saúde! Você pode conferir agora!"
          this.talkAnimation(quote);
          this.achievementIcons.toArray()[parseInt(achievement.icon)].nativeElement.className = "sonar-wave";
          setTimeout(() => {
            this.achievementIcons.toArray()[parseInt(achievement.icon)].nativeElement.classList.remove("sonar-wave");
          }, 10000);
        }
      }, {
        text: 'Ver a dica!',
        cssClass: 'achievement-alert-button-confirm',
        handler: () => {
          this.restProvider.sendGoogleAnalyticsEventTag(
            'Questionary List',
            'Clicou na conquista pela dialog | ' + achievement.name,
            '',
            'Game');
          this.storage.set('achievementList', this.achievementList).then(() => {
            this.getAchievement(achievement);
          });
        }
      }]
    });
    alert.present();
  }

  getAchievement(achievement: Achievement) {
    this.restProvider.sendGoogleAnalyticsEventTag(
      'Questionary List',
      'Clicou na conquista | ' + achievement.name,
      '',
      'Game');
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.navCtrl.push('AchievementPage', {
      achievement: achievement,
      plan: this.plan,
    }).then(this.loader.dismiss());
  }

  resolvePerson() {
    this.personAnimation = "gif-a-4-crop";
    this.personQuote = "Responda os questionários para conseguir <strong>pontos</strong> para liberar as <strong>dicas de saúde</strong>! Você só poderá responder cada questionário uma vez.";
  }

  talkAnimation(quote) {
    this.personAnimation = "gif-a-5-crop";
    this.personQuote = quote;
    setTimeout(() => {
      this.resolvePerson();
    }, 10000);
  }

  //Mensagem motivadora da narrativa
  help() {
    let alert = this.alertCtrl.create({
      title: '<div text-center>Olá! Bem vindo(a) ao Opina Aí</div>',
      message:
        //----------------------MENSAGEM------------------
        '<div class="alert-align-center">'
        + '<img class="img-alert" src="assets/gifs/gif-a-3.webp"/>'
        + '</div>'
        + '<div class="dialogue-box"><div class="tdialogue-box-text">'
        + '<div><strong>Bem vindo(a) ao Opina Aí!</strong></div>'
        // + '<div text-center>Sinta-se a vontade para contribuir com sua <strong>cidade</strong> respondendo os questionários!</div>'
        // + '<div text-center>Sua <strong>participação</strong> é muito importante!</div>'
        // + '</div></div>',
        + '<div text-center>Sinta-se a vontade para contribuir com a pesquisa para o <strong>levantamento acerca da saúde mental</strong> respondendo os questionários!</div>'
        + '<div text-center>Sua <strong>participação</strong> é muito importante!</div>'
        + '</div></div>',
      //----------------------MENSAGEM------------------
      buttons: [{
        text: "Entendi, quero participar!",
        handler: () => {
          this.storage.get('helpHome')
            .then((data) => {
              if (data == null) {
                this.presentToast();
                this.storage.set('helpHome', true);
              }
            });
        }
      }]
    });
    alert.present();
  }

  presentToast() {
    const toast = this.toastCtrl.create({
      message: 'Clique no icone em caso de dúvidas!',
      duration: 4000,
      position: 'top'
    });
    toast.present();
  }

  openMenu() {
    this.menuCtrl.open();
  }

}

export class Achievement {
  icon: string;
  unlocked: boolean;
  name: string;
  id: number;
}


